<template>
  <div>
    <div class="btn-gp">
      <span id="keywords">Key words: </span>

      <b-button size="sm" id="ref-btn" variant="outline-primary"
                v-for="key in ks" :key="key"
                @click="showModal(key)">{{ $t(key) }}</b-button>
    </div>

    <b-modal size="xl" ref="details" hide-footer :title="$t(sel.title)" title-tag="h3">
      <div class="d-block text-center">
        <html-card :url="sel.url[$i18n.locale]"></html-card>
      </div>
    </b-modal>
  </div>
</template>

<script>
  import HtmlCard from "./Cards/HtmlCard";
  import Keys from "@/lists/topics";

  const get_page = function(key, id) {
    return {
      title: key,
      id: id,
      url: {
        en_UK: `https://covid-19-modelling.github.io/DashboardData/Docs/Learn_${ id }_en.html`,
        zh_TW: `https://covid-19-modelling.github.io/DashboardData/Docs/Learn_${ id }_tw.html`
      }
    };
  };

  export default {
    name: "Popup",
    components: {
      HtmlCard
    },
    props: {
      ks: {
        type: Array,
        default() {
          return ["indices.rt"];
        }
      }
    },
    methods: {
      showModal(key) {
        const topic = this.topics[key];
        this.sel = get_page(key, topic);
        this.$refs["details"].show()
      }
    },
    data() {
      let topics = {};

      for (let i = 0; i < this.ks.length; i++) {
        let k = this.ks[i].split(".");

        if (Keys[k[0]] === undefined || Keys[k[0]][k[1]] === undefined || Keys[k[0]][k[1]] === "") continue;
        topics[this.ks[i]] = Keys[k[0]][k[1]];
      }

      return {
        sel: get_page("", "Rt"),
        topics
      };
    }
  }
</script>

<style scoped>
  @media screen and (max-width: 1000px) {
    .btn-gp {
      visibility: hidden;
      clear: both;
      float: left;
      margin: 10px auto 5px 20px;
      width: 28%;
      display: none;
    }
  }

  .btn-gp {
    position: fixed;
    bottom: 20px;
    left: 350px;
    z-index: 64;
    background-color: #eee;
  }

  #ref-btn {
    font-size: 16pt;
  }

  #keywords {
    font-size: 16pt;
  }
</style>
